.blog {
  &__container {
    max-width: 980px !important;
  }

  &__title {
    line-height: 1.4;
    letter-spacing: normal;
    margin: 1rem 0 1.6rem 0;
  }

  &__content {
    margin-top: 3.4rem;

    &.nontop {
      margin-top: 0;
    }

    h2 {
      color: $yellow;
      border-bottom: 1px solid $yellow;
      padding-bottom: 1rem;
      margin-bottom: 1.6rem;

      strong {
        color: $yellow;
      }
    }

    p,
    ul {

      &+h2,
      &+h3,
      &+h4 {
        margin-top: 1.6rem;
      }
    }

    ul {
      padding-left: 1.6rem;

      li {
        line-height: 1.8;
      }
    }
  }
}

.notetop {
  border: 1px solid $yellow;
  @include align-items(center);
  padding: 0.5rem 2rem;
  text-align: center;

  @include breakpoint-up(sm) {
    text-align: left;
  }

  .wp-block-column {
    margin-left: 0 !important;
  }

  p {
    text-transform: uppercase;
  }

  a {
    color: $yellow !important;
    text-decoration: underline;
  }
}

.gform {
  &--tit {
    font-family: $font2;
    font-size: 2rem;
    color: $yellow;

    @include breakpoint-up(md) {
      font-size: 3rem;
    }
  }

  .contactForm__input {
    border: 1px solid $black_light !important;
    padding: 2rem 1.5rem !important;
    margin: 1.2rem 0;
    font-size: 1.4rem;

    &.required {
      border-color: red;
    }
  }

  .contactForm__submit {}
}

.service__popup__submit {
  @include transition_default(0.3s);
  position: relative;

  &::before {
    content: '';
    width: 60px;
    height: calc(100% - 4px);
    display: block;
    position: absolute;
    left: 2px;
    top: 2px;
    background: url('../images/icon-calendar.png') no-repeat $black center;
    background-size: 32px 32px;
    z-index: 2;
  }

  &:hover {
    .contactForm__submit {
      border-color: $yellow;
      color: $white;
    }
  }

  .contactForm__submit {
    z-index: 1;
    color: $white;
    font-family: $font2 !important;
    font-size: 2rem;
    cursor: pointer;
    background: url('../images/icon-calendar.png') no-repeat $yellow left 1rem center;
    background-size: 32px 32px;
    border: 2px solid $black;
    position: relative;
    @include transition_default(0.3s);
    margin: 0;
  }
}

.open__service__popup {
  margin: 2rem 1rem !important;

  a {
    background-color: $yellow;
    border-radius: 0;
    position: relative;
    padding-left: 70px;
    border: 2px solid $black;
    text-transform: uppercase;
    font-family: $font2;
    font-weight: 700;

    &:hover {
      border-color: $yellow;
      color: $white;
    }

    &::before {
      content: '';
      width: 60px;
      height: 100%;
      display: block;
      position: absolute;
      left: 0;
      top: 0;
      background: url('../images/icon-calendar.png') no-repeat $black center;
      background-size: 32px 32px;
    }
  }
}